import React, { Fragment } from "react";
import cx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardMedia from "@material-ui/core/CardMedia";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import Button from "@material-ui/core/Button";
import TextInfoContent from "@mui-treasury/components/content/textInfo";
import { useFourThreeCardMediaStyles } from "@mui-treasury/styles/cardMedia/fourThree";
import { useN04TextInfoContentStyles } from "@mui-treasury/styles/textInfoContent/n04";

const useStyles = makeStyles(() => ({
  root: {
    maxWidth: 343,
    margin: "auto",
    borderRadius: 30,
    padding: 12,
    background: "#ECF0F1",
    boxShadow: "20px 20px 60px #c9cccd,-20px -20px 60px #ffffff",
  },
  media: {
    borderRadius: 6,
  },
}));

export const CardDemoMemozied = React.memo(function CardDemo({
  TechStack,
  ProjectName,
  ProjectImage,
  About,
  DemoLink,
  CodeLink,
}) {
  const styles = useStyles();
  const mediaStyles = useFourThreeCardMediaStyles();
  const textCardContentStyles = useN04TextInfoContentStyles();

  return (
    <Fragment>
      <Card className={cx(styles.root)} elevation={0}>
        <CardMedia
          className={cx(styles.media, mediaStyles.root)}
          style={{ borderRadius: 20 }}
          image={ProjectImage}
        />
        <CardContent>
          <TextInfoContent
            classes={textCardContentStyles}
            overline={TechStack}
            heading={ProjectName}
          />
        </CardContent>
        <CardActions
          disableSpacing
          style={{ display: "flex", justifyContent: "center" }}
        >
          <Button aria-label="demo" href={DemoLink} target="__blank">
            Demo
          </Button>
          <Button aria-label="demo" href={CodeLink} target="__blank">
            Code
          </Button>
        </CardActions>
      </Card>
    </Fragment>
  );
});
export default CardDemoMemozied;
